<template>
  <div class="sub_module">
    <div class="module_header">
      <div class="title">随堂练习</div>
    </div>

    <Table ref="table" :ShowTopBar="false" :ShowTblFoot="false" :ShowSelectColumn="false" :ShowExport="false"
           :Columns="tableColumns" :Datas.sync="dataList" :Loading="dataLoading" :showMaxeight="false">
      <template slot="oprate" slot-scope="scope" align="center">
        <el-button type="primary" size="mini" @click="previewAnswer(scope.row)" style="padding: 7px 15px;">查看答题
        </el-button>
        <el-button type="success" size="mini" @click="getScoreStats(scope.row)" style="padding: 7px 15px;">得分统计
        </el-button>
      </template>
    </Table>

    <el-dialog title="答题明细" :visible.sync="dialogDetailVisible" :close-on-click-modal="false"
               :modal-append-to-body="false">
      <practice-detail ref="PracticeDetail" :cpdata="selectData"></practice-detail>
    </el-dialog>

    <el-dialog title="得分统计" :visible.sync="dialogScoreVisible" :close-on-click-modal="false"
               :modal-append-to-body="false">
      <el-table :data="dialogScoreList" max-height="550" v-loading="dialogScoreLoading" stripe
                :default-sort="{prop: 'Ranking', order: 'ascending'}">
        <el-table-column property="Ranking" label="排名"></el-table-column>
        <el-table-column property="StudentName" label="学生姓名"></el-table-column>
        <el-table-column property="SingleScore" label="单选题"></el-table-column>
        <el-table-column property="MultipleScore" label="多选题"></el-table-column>
        <el-table-column property="JudgeScore" label="判断题"></el-table-column>
        <el-table-column property="QandAScore" label="问答题"></el-table-column>
        <el-table-column property="TotalScore" label="总分"></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/scripts/api";
import { getSession } from "@/scripts/utils";

export default {
  name: "ClassPractice",
  components: {
    Table: () => import("@/components/Table"),
    PracticeDetail: () => import("./PracticeDetail")
  },
  data () {
    return {
      courseRecord: getSession("campus_CourseRecord"),
      courseRecordID: getSession("campus_CourseRecordID"),
      dataList: [],
      dataLoading: false,
      tableColumns: [
        { Code: "CPTitle", Header: "名称", Width: 300 },
        { Code: "SubjectTotal", Header: "题数" },
        { Code: "SubjectScore", Header: "总分" },
        { Code: "CPTime", Header: "计时(分)" },
        { Code: "oprate", Header: "操作", Slot: true, Width: 450 }
      ],

      dialogScoreVisible: false,
      dialogScoreList: [],
      dialogScoreLoading: false,

      dialogDetailVisible: false,
      selectData: {}
    };
  },
  created () {
    this.getClassPracticeStats();
  },
  methods: {
    // 获取随堂练习
    getClassPracticeStats () {
      this.dataLoading = true;
      this.dataList = [];
      api.IClassPractise.dataStatistics(this.courseRecordID).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataList.push({
              CIPID: item.CIPID,
              CPID: item.Key.CPID,
              PLID: item.Key.PLID,
              CPTitle: item.PLTitle,
              SubjectTotal: item.Total,
              SubjectScore: item.Score,
              CPTime: item.CIPTime,
              CreateTime: item.Key.CreateTime,
              State: item.Key.State
            });
          });
          this.$refs["table"].MaxTblHeight = 500;
        }
        this.dataLoading = false;
      }).catch(() => {
        this.dataLoading = false;
      });
    },

    getScoreStats (row) {
      this.dialogScoreVisible = true;
      this.dialogScoreList = [];
      this.dialogScoreLoading = true;
      api.IClassPractise.PractiseScoreStatistics(this.courseRecord.classID, row["CIPID"]).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dialogScoreList.push(item);
          });
        }
        this.dialogScoreLoading = false;
      }).catch(() => {
        this.dialogScoreLoading = false;
      });
    },

    previewAnswer (row) {
      this.selectData = row;
      this.dialogDetailVisible = true;
      setTimeout(() => {
        this.$refs["PracticeDetail"].getSubjectData();
      }, 100);
    }
  }
};
</script>

<style lang="less" scoped>
@import "./../../style/record";
</style>
