<template>
  <div class="class-practice">
    <div class="item-hd">
      <div class="line-title">随堂练习</div>
      <div>
        <el-button type="primary" size="mini" @click="searchData">刷新</el-button>
        <el-button type="primary" size="mini" @click="$router.push('/p/mq')">我的题库</el-button>
        <el-button type="primary" size="mini" :disabled="disabled" @click="addData">
          {{attribute === 2 ? '练习添加' : '布置练习'}}
        </el-button>
      </div>
    </div>

    <Table ref="table" :ShowTopBar="false" :ShowTblFoot="false" :ShowSelectColumn="false" :ShowExport="false"
           :Columns="dataColumn" :Loading="searchLoading" :Datas.sync="dataList" :UnNeedEstateBtn="true"
           @LoadData="searchData" :showMaxeight="false">
      <template slot="className" slot-scope="scope">
        <div style="min-width: 130px; white-space: normal;">{{scope.row.className }}</div>
      </template>
      <template slot="createTime" slot-scope="scope">
        <span>{{scope.row.createTime | formatDate(scope.row.createTime)}}</span>
      </template>
      <template slot="operate" slot-scope="scope">
        <el-button-group>
          <el-button plain type="success" size="mini" icon="el-icon-edit" onfocus="this.blur()" :disabled="disabled"
                     @click="editData(scope.row)">
            修改
          </el-button>
          <el-button plain type="warning" size="mini" icon="el-icon-view" onfocus="this.blur()"
                     @click="beforeShowPreview(scope.row)">
            预览
          </el-button>
          <el-button plain type="danger" size="mini" icon="el-icon-close" onfocus="this.blur()" :disabled="disabled"
                     @click="dataDrop(scope.row)">
            移除
          </el-button>
          <el-button plain v-if="scope.row.attribute !== 2" type="primary" size="mini" icon="el-icon-time"
                     onfocus="this.blur()" @click="studentTask(scope.row)">
            学生完成进度
          </el-button>
        </el-button-group>
      </template>
    </Table>

    <el-dialog :title="dataEdit ? '随堂练习修改' : '随堂练习创建'" :visible.sync="showEdit" :close-on-click-modal="false"
               :modal-append-to-body="false" width="600px" center @close="closeDialog">
      <el-form ref="form" :model="form" :rules="rulesTitle" label-width="80px">
        <el-form-item label="试卷题库" prop="libraryID">
          <el-select v-model="form.libraryID" placeholder="请选择试卷题库" style="width: calc(100% - 80px);">
            <el-option v-for="(item, index) in practiceLibraryList" :key="index" :label="item.label"
                       :disabled="usedLibrary.indexOf(item.value)>-1 && item.value !== form.libraryID"
                       :value="item.value">
            </el-option>
          </el-select>
          <el-button type="primary" size="small" @click="getPracticeLibraryList" style="margin-left: 15px;">刷新
          </el-button>
        </el-form-item>

        <el-form-item label="布置班级" prop="classID" v-if="attribute !== 2">
          <el-select v-model="form.classID" multiple placeholder="请选择布置班级" style="width:100%;">
            <el-option v-for="(item, index) in classOptions" :key="index" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <!--        <el-form-item label="答题计时" prop="time">-->
        <!--          <el-input-number v-model="form.time" :min="1" :max="1000" label="试卷计时" controls-position="right"-->
        <!--                           :precision="0" :step="1"></el-input-number>-->
        <!--          分钟-->
        <!--        </el-form-item>-->
        <div style="text-align:right;">
          <el-button size="small" @click="showEdit = false">取消</el-button>
          <el-button size="small" :loading="btnLoading" type="primary" @click="saveForm('form')">保存</el-button>
        </div>
      </el-form>
    </el-dialog>

    <el-dialog title="随堂练习预览" :visible.sync="showPreview" :close-on-click-modal="false" :modal-append-to-body="false"
               width="800px" center @close="closePreviewDialog">
      <div class="pre_title" style="margin-top: -20px;">
        <span>题库名称：</span>
        <span>{{dataInfo.title}}</span>
      </div>
      <div class="pre_line">
        <span>试卷计时：</span>
        <span>{{dataInfo.time}}</span>
        <span>总题数：</span>
        <span>{{dataInfo.subjectList.length}}</span>
        <span>总分数：</span>
        <span>{{dataInfo.score}}</span>
      </div>

      <el-collapse v-model="dataInfo.subjectActive" class="collapse_main" accordion v-loading="libraryViewLoading">
        <el-collapse-item v-for="(item, index) in dataInfo.subjectList" :key="index" :name="index">
          <template slot="title">
            <el-row :gutter="20" class="collapse_head">
              <el-col :span="24">
                <span>{{ (index+1)+'、['+ subjectTypeOptions[item.type-1].label +']'+ item.title}}</span>
              </el-col>
            </el-row>
          </template>
          <el-card class="collapse_card">
            <div v-if="[1, 2].indexOf(item.type) > -1">
              <h4>选项：</h4>
              <p v-for="(sub, k) in item.options" :key="k">{{sub.value +'、'+sub.label}}</p>
            </div>
            <h4>答案：</h4>
            <div class="card_line">
              <span>正确答案：</span>
              <span>{{item.type===2?item.multiAnswer.join(''):item.answer}}</span>
              <span>分数：</span>
              <span>{{item.score}}</span>
            </div>
            <h4>解析：</h4>
            <div class="card_content">{{item.analysis}}</div>
          </el-card>
        </el-collapse-item>
      </el-collapse>
      <div style="text-align:right;margin-top:15px;">
        <el-button size="small" @click="showPreview=false">关 闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import api from "@/scripts/api";
import { getSession, setSession } from "@/scripts/utils";

export default {
  components: {
    Table: () => import("@/components/Table")
  },
  props: {
    attribute: {
      type: Number,
      default: 1
    },
    disabled: {
      type: Boolean,
      default: false
    },
    classOptions: {
      type: Array,
      default: () => {
        return [];
      }
    },
    // 已经被选取的题库ID
    usedLibrary: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data () {
    return {
      classHourID: getSession("campus_ClassHourID"),
      showEdit: false,
      editTitle: true, // 默认编辑标题
      showPreview: false,
      searchLoading: false,
      dataList: [],
      practiceLibraryList: [],
      form: {
        id: "",
        libraryID: "",
        attribute: "",
        classID: [],
        time: 0
      },
      rulesTitle: {
        libraryID: {
          required: true,
          message: "请选择试卷题库",
          trigger: "change"
        },
        classID: {
          required: true,
          message: "请选择布置班级",
          trigger: "change"
        },
        time: [
          {
            required: true,
            message: "试卷计时不能为空"
          },
          {
            type: "number",
            message: "试卷计时必须为数字值"
          }
        ]
      },
      attributeOptions: [
        {
          label: "课前",
          value: 1
        },
        {
          label: "课中",
          value: 2
        },
        {
          label: "课后",
          value: 3
        }
      ],
      subjectTypeOptions: [
        {
          label: "单选题",
          value: 1
        },
        {
          label: "多选题",
          value: 2
        },
        {
          label: "判断题",
          value: 3
        },
        {
          label: "问答题",
          value: 4
        }
      ],
      singleOptions: ["A", "B", "C", "D", "E", "F", "G", "H"],
      judgeOptions: ["对", "错"],
      btnLoading: false,
      dialogChange: false, // 对话框是否有更新事件，此参数用来更新查询列表
      dataEdit: false, // 是否修改试卷
      libraryViewLoading: false,
      dataInfo: {
        id: "",
        title: "",
        time: 0,
        score: 0,
        subjectActive: 0,
        subjectList: []
      }
    };
  },
  computed: {
    dataColumn () {
      const columns = [
        {
          Code: "title",
          Header: "题库名称",
          Fixed: "left"
        },
        {
          Code: "createTime",
          Header: "创建时间",
          Slot: true
        },
        {
          Code: "operate",
          Header: "操作",
          Slot: true,
          Width: 450
        }
      ];
      if (this.attribute !== 2) {
        columns.splice(1, 0, {
          Code: "className",
          Header: "布置班级",
          Slot: true,
          Fixed: "left"
        });
      }
      return columns;
    }
  },
  created () {
    this.searchData();
    this.getPracticeLibraryList();
  },
  methods: {
    closeDialog () {
      if (this.dialogChange) this.searchData();
    },
    /**
     * 获取题库，每次编辑刷新一遍
     */
    getPracticeLibraryList () {
      this.practiceLibraryList = [];
      api.IClassPractise.LibraryDataList().then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.practiceLibraryList.push({
              label: item["PLTitle"] || "",
              value: item["PLID"] || ""
            });
          });
        }
      }).catch(() => {
      });
    },
    searchData () {
      this.searchLoading = true;
      this.dataList = [];
      const libraryIDList = [];
      api.IClassPractise.DataList(this.classHourID, this.attribute).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataList.push({
              id: item.Datas["CPID"] || "",
              libraryID: item.Datas["PLID"] || "",
              attribute: item.Datas["Attribute"],
              // time: item.Datas["CPTime"] || "",
              state: item.Datas["State"] || 0,
              createTime: item.Datas["CreateTime"] || "",
              title: item["Title"] || "",
              classID: item["ClassID"] || "",
              className: item["ClassName"] || "",
              taskId: item["CIPID"] || ""
            });
            libraryIDList.push(item.Datas["PLID"] || "");
          });
          this.$refs["table"].MaxTblHeight = 500;
        }
        this.searchLoading = false;
        this.$emit("load", libraryIDList);
      }).catch(() => {
        this.searchLoading = false;
        this.$emit("load", libraryIDList);
      });
    },
    resetForm (form) {
      if (this.$refs["form"]) this.$refs["form"].resetFields();
      console.log(form);
      this.form = {
        id: form ? form.id : "",
        libraryID: form ? form.libraryID : "",
        classID: form ? form.classID.split(",") : []
        // time: form ? form.time : 0
      };
    },
    addData () {
      // this.dataEdit = false;
      this.resetForm();
      this.showEdit = true;
    },
    editData (row) {
      this.dataEdit = true;
      this.resetForm(row);
      this.showEdit = true;
    },
    saveForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.btnLoading = true;
          let classID = "";
          if (this.attribute !== 2) classID = this.form.classID.join(",");

          api.IClassPractise.DataSave(this.form.id, this.classHourID, this.form.libraryID, this.attribute, classID).then(res => {
            if (res.Result) {
              this.dialogChange = true;
              this.showEdit = false;
              this.$message.success(this.form.id ? "修改成功！" : "创建成功！");
            } else if (res.Info) {
              this.$message.error(res.Info);
            }
            this.btnLoading = false;
          }).catch(() => {
            this.btnLoading = false;
          });
        } else {
          this.$message.warning("请输入正确格式");
          return false;
        }
      });
    },
    beforeShowPreview (row) {
      this.getDataInfo(row);
      this.showPreview = true;
    },
    getDataInfo (row) {
      this.dataInfo.subjectList = [];
      this.dataInfo.title = row.title || "";
      this.dataInfo.time = row.time || 0;
      this.dataInfo.score = 0;
      this.libraryViewLoading = true;
      api.IClassPractise.LibraryDataView(row.libraryID).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataInfo.subjectList.push({
              libraryID: item.Key["PLID"],
              id: item.Key["PSID"],
              no: item.Key["PSSerial"] || 0,
              title: item.Key["PSContent"],
              type: item.Key["PSType"],
              score: item.Key["PSScore"] || 0,
              analysis: item.Key["PSanalysis"],
              answer: item.Key["PSAnswer"],
              multiAnswer: item.Key["PSType"] !== 2 ? [] : item.Key["PSAnswer"].split(""),
              options: item.Key["PSType"] === 4 ? [] : item.Value.map(v => {
                return {
                  label: v["SOContent"],
                  value: v["SOSerial"]
                };
              })
            });
            this.dataInfo.score += item.Key["PSScore"] || 0;
          });
        }
        this.libraryViewLoading = false;
      }).catch(() => {
        this.libraryViewLoading = false;
      });
    },
    dataDrop (row) {
      this.$confirm("此操作将删除随堂练习, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.classID = row.classID;
        this.dataDelete(row);
      });
    },
    dataDelete (row) {
      api.IClassPractise.DataDelete(row.id).then(res => {
        if (!res.Result) {
          if (res.Info) this.$message.error(res.Info);
        } else {
          this.$message.success("删除成功");
          this.searchData();
        }
      }).catch(() => {
        this.$message.error("操作失败");
      });
    },

    // 关闭编辑和预览窗体
    closePreviewDialog () {
      this.dataEdit = false;
    },
    studentTask (row) {
      setSession("campus_ClassPractice", row);
      setSession("campus_TaskType", "ClassPractice");
      this.$router.push("/p/task");
    }
  }
};
</script>
<style lang="less">
.class-practice {
  .pre_title {
    font-size: 15px;
    font-weight: bold;
    line-height: 2;

    .title_btn {
      display: inline-block;
      padding-left: 30px;
    }
  }

  /deep/ .el-collapse-item__header {
    min-height: 48px;
    height: unset;
    line-height: 1.5;
  }

  .pre_line {
    & span:nth-child(2n) {
      margin-right: 30px;
    }
  }

  .collapse_main {
    margin-top: 20px;

    .collapse_head {
      width: 100%;

      span {
        font-size: 15px;
        line-height: 1.5;
      }
    }

    .collapse_card {
      font-size: 14px;

      h4 {
        font-size: 16px;
        border-left: 5px solid #59d8ff;
        padding-left: 10px;
      }

      p {
        font-size: 14px;
        line-height: 1.5;
        margin-bottom: 0;
        margin-left: 20px;
      }

      .card_line {
        margin-left: 20px;
        line-height: 2;

        span:nth-child(2n) {
          color: #f56c6c;
          background: #f8f8f8;
          padding: 0 5px;
          margin-right: 30px;
        }
      }

      .card_content {
        margin-left: 20px;
        padding: 3px 10px;
        color: #f56c6c;
        background: #f8f8f8;
      }
    }
  }
}
</style>
