<template>
  <div class="practise_view">
    <div class="practise_main">
      <div class="head">
        <div>{{dataInfo['Title']}}</div>
        <div>总分：{{score}}</div>
        <div>习题数：{{total||'0'}}</div>
        <div>
          时间: {{dataInfo['CreateTime'] }}
        </div>
      </div>

      <!--  时间轴形式-->
      <el-timeline v-if="dataList && dataList.length>0">
        <el-timeline-item placement="top" v-for="(item, i) in dataList" :key="i"
                          :timestamp="(i+1) + '.【' + getPracticeTypeName(item.practiceInfo['PSType']) + '】'+(item.practiceInfo['PSScore']||0) + '分'">
          <el-card>
            <div class="title_info">【题目】{{item.practiceInfo['PSContent']}}</div>
            <div v-if="[1, 2].indexOf( item.practiceInfo['PSType'])>-1">
              <div class="options" v-for="(sub, k) in item.practiceOptions" :key="k">
                <span>【{{ sub['SOSerial']}}】</span>
                <span>{{sub['SOContent']}}</span>
              </div>
            </div>

            <my-answer :question-type="item.practiceInfo['PSType']"
                       :my-answer="item.practiceAnswer"
                       :module-picture="modulePicture"
                       :true-answer="item.practiceInfo['PSAnswer']"
                       :analysis="item.practiceInfo['PSanalysis']"
                       hiddenScore
                       :get-score="item.practiceReply? item.practiceReply['PAScore'] : 0">
            </my-answer>

            <div class="survey_info" v-show="false">
              <span>问卷调研：</span>
              <el-button size="small" style="padding: 12px 20px;" icon="el-icon-check" type="primary" disabled
                         v-if="item.surveyMaster===1">已掌握
              </el-button>
              <el-button size="small" style="padding: 12px 20px;" icon="el-icon-close" type="danger" disabled
                         v-else-if="item.surveyMaster===0">
                未掌握
              </el-button>
              <el-button size="small" style="padding: 12px 20px;" type="warning" disabled v-else>未参与调研</el-button>
            </div>
          </el-card>
        </el-timeline-item>
      </el-timeline>
      <r-lack-data v-else></r-lack-data>
      <div style="text-align: right;padding: 15px 0;">
        <el-button style="padding: 12px 20px;" @click="$emit('toClose')">关闭</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/scripts/api";
import store from "@/scripts/store";
import { practiceTypeName, ModulePicture } from "@/scripts/dictionary";
import axios from "axios";

export default {
  name: "",
  components: {
    MyAnswer: () => import("./MyAnswer")
  },
  props: {
    dataInfo: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data () {
    return {
      fromID: this.dataInfo["CPID"] + "",
      bankID: this.dataInfo["PLID"] + "",
      taskId: this.dataInfo["CIPID"] + "",

      total: 0,
      score: 0,
      modulePicture: ModulePicture.PractiseAnswer,
      dataList: [],

      studentInfo: null
    };
  },
  created () {
    this.studentInfo = store.GetStudentInfo();
  },
  methods: {
    init () {
      this.total = 0;
      this.score = 0;
      this.dataList = [];
      this.taskId = this.dataInfo["CIPID"] + "";
      this.fromID = this.dataInfo["CPID"] + "";
      this.libraryId = this.dataInfo["PLID"] + "";
      this.getPracticeInfo();
    },
    getPracticeInfo () {
      api.IClassPractise.StudentTaskStatistics(
        this.dataInfo["PLID"] + ""
      ).then(res => {
        if (res.Result) {
          this.total = res.Total;
          this.score = res.Score;
          this.getAllDataInfo();
        } else {
        }
      }).catch(() => {
      });
    },
    getAllDataInfo () {
      const requestAll = [];

      for (let i = 1; i <= this.total; i++) {
        requestAll.push(
          api.IClassPractise.StudentTaskInfo(
            this.taskId,
            this.libraryId,
            i,
            this.studentInfo ? this.studentInfo.StudentID : ""
          )
        );
      }

      axios.all(requestAll).then(
        axios.spread((...resList) => {
          resList.forEach((res, i) => {
            if (res.Result) {
              const practiceInfo = res.PSData;
              const practiceReply = res.PAData ? res.PAData : {};
              const practiceAnswer = practiceReply["PAContent"] || "";
              let surveyMaster = null;
              if (practiceReply) {
                surveyMaster = practiceReply["PAMaster"] || null;
              }

              const practiceOptions = [];
              if (res.SOData && res.SOData.length > 0) {
                res.SOData.forEach(item => {
                  if (practiceOptions.indexOf(item) === -1) {
                    practiceOptions.push(item);
                  }
                });
              }
              this.dataList.push({
                practiceInfo,
                practiceReply,
                practiceAnswer,
                surveyMaster,
                practiceOptions
              });
            } else {
              this.dataList.push({});
            }
          });
        })
      );
    },
    getPracticeTypeName (val) {
      return practiceTypeName(val);
    }
  }
};
</script>

