<template>
  <div class="sckviewer">
    <Calendar @Changed="DateChanged" :ScheduledDate.sync="LogDate" :ChooseDate.sync="ChooseDate" />
    <p class="title">{{FormatDate(ChooseDate)}}门禁信息</p>
    <div class="lists" v-if="logs">
      <ul>
        <li v-for="(item,index) in logs" :key="index">
          <div :class="{prohibit:item.cardtype===0}">
            <span>{{item.logTime}}</span>
          </div>
          <div>
            <p>
              <span>卡类型：{{item.type}}</span>
              <span>{{item.valid}}</span>
            </p>
            <p>
              <span>教师：{{item.teacher}}</span>
            </p>
            <p>
              <span>卡号：{{item.card}}</span>
            </p>
          </div>
        </li>
      </ul>
    </div>
    <div v-if="!logs" class="none">
      <p>{{FormatMonthDay(ChooseDate)}} 无日志</p>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import api from "@/scripts/api";
export default {
  components: {
    Calendar: () => import("@/components/Calendar")
  },
  props: { TerminalInfo: { type: Object, default: () => null } },
  data() {
    return {
      LogDate: [],
      ChooseDate: new Date(),
      logs: null
    };
  },
  mounted() {
    var sDate = this.MonthHeadDate(this.ChooseDate);
    var eDate = this.MonthEndDate(this.ChooseDate);
    var sLogDate = this.DayHeadDate(this.ChooseDate);
    var eLogDate = this.DayEndDate(this.ChooseDate);
    this.LoadData(sDate, eDate, sLogDate, eLogDate);
  },
  methods: {
    FormatDate(day) {
      return moment(day).format("YYYY-MM-DD");
    },
    FormatMonthDay(day) {
      return moment(day).format("MM-DD");
    },
    MonthHeadDate(date) {
      return moment(date).format("YYYY-MM") + "-01 00:00:00";
    },
    MonthEndDate(date) {
      return (
        moment(date).format("Y-MM") +
        "-" +
        moment(moment(date).format("Y-MM"), "Y-MM").daysInMonth() +
        " 23:59:59"
      );
    },
    DayHeadDate(date) {
      return moment(date).format("YYYY-MM-DD") + " 00:00:00";
    },
    DayEndDate(date) {
      return moment(date).format("YYYY-MM-DD") + " 23:59:59";
    },
    DateChanged(date) {
      var sDate = this.MonthHeadDate(date);
      var eDate = this.MonthEndDate(date);
      var sLogDate = this.DayHeadDate(date);
      var eLogDate = this.DayEndDate(date);
      this.LoadData(sDate, eDate, sLogDate, eLogDate);
    },
    async LoadData(sDate, eDate, sLogDate, eLogDate) {
      const roomid = this.$parent.$route.params.id;
      const res = await api.Ctrl.GetEntranceLogByDate(
        roomid,
        sDate,
        eDate,
        sLogDate,
        eLogDate
      );
      this.LogDate = [];
      this.logs = null;
      if (res.Result) {
        if (res.Dates && res.Dates.length > 0) {
          res.Dates.forEach(element => {
            this.LogDate.push(element.logTime);
          });
        }
        if (res.LogInfo && res.LogInfo.length > 0) {
          this.logs = [];
          res.LogInfo.forEach(element => {
            var cardtype = "";
            var valid = "通过";
            switch (element.CardType) {
              case 0:
                cardtype = "无效卡";
                valid = "禁止";
                break;
              case 1:
                cardtype = "教师卡";
                break;
              case 2:
                cardtype = "白名单";
                break;
              case 3:
                cardtype = "网管操作";
                break;
            }
            this.logs.push({
              logTime: moment(element.logTime).format("HH:mm:ss"),
              type: cardtype,
              cardtype: element.CardType,
              valid: valid,
              teacher: element.TeacherName,
              card: element.CardNo
            });
          });
        }
      } else {
        this.$message({
          showClose: true,
          message: res.Info,
          type: "error",
          duration: 1000
        });
      }
    }
  }
};
</script>

<style lang="less">
.sckviewer {
  .none {
    > p {
      font-size: 48px;
      text-align: center;
      line-height: 300px;
      color: #c7cdd3;
    }
  }
  .prohibit {
    background-color: @RedColor !important;
  }
}
</style>