<template>
  <div>
    <!-- 单选题 -->
    <div class="selects" v-if="questionType===1">
      <div class="content">
        <div class="prompt">请选择正确的答案：</div>
        <div class="all-select">
          <div class="select-item" v-for="(item, i) in selectOptions" :key="i"
               :class="answerValue === item? 'single-selected':''" @click="selectAnswer(item)">
            {{item}}
          </div>
        </div>
      </div>
    </div>

    <!-- 多选题 -->
    <div class="selects" v-else-if="questionType===2">
      <div class="content">
        <div class="prompt">请选择正确的答案：</div>
        <div class="all-select">
          <div class="select-item" v-for="(item, i) in selectOptions" :key="i"
               :class="answerValue.indexOf(item) > -1 ? 'single-selected' : ''" @click="selectAnswer(item)">
            {{item}}
          </div>
        </div>
      </div>
    </div>

    <!-- 判断题 -->
    <div class="answers" v-else-if="questionType===3">
      <div class="remark">请选择正确的答案：</div>
      <div class="buttons">
        <div v-for="(item, i) in selectOptions" :key="i" :class="answerValue === item ? 'selected' : ''"
             @click="selectAnswer(item)">
          {{item}}
        </div>
      </div>
    </div>

    <!-- 问答题 -->
    <div class="answers" v-else>
      <div class="remark">请将您的答案写在下方的编辑框中</div>
      <r-quill-editor :funType="modulePicture" :content="selectValue" @change="editorChange"
                      max-height="400px">
      </r-quill-editor>
    </div>
    <slot></slot>
  </div>
</template>
<script>

import { newsetDesc } from "@/scripts/utils";

export default {
  components: {
    RQuillEditor: () => import("@/components/RQuillEditor")
  },
  props: {
    questionType: {
      type: Number,
      default: 1
    },
    questionOption: {
      type: Number,
      default: 0
    },
    btnName: {
      type: String,
      default: "提交"
    },
    ableReply: {
      type: Boolean,
      default: true
    },
    modulePicture: {
      type: Number,
      default: 1
    },
    answerValue: {
      type: String,
      default: ""
    }
  },
  data () {
    return {
      // singleChoice: ["A", "B", "C", "D"],
      // multiChoice: ["A", "B", "C", "D"],
      judgeChoice: ["对", "错"],
      multiValue: this.selectValue ? this.selectValue.split("") : [],
      selectValue: this.answerValue
    };
  },
  mounted () {
  },
  methods: {
    selectAnswer (item) {
      if (!this.ableReply) return;
      if (this.questionType === 2) {
        const index = this.multiValue.indexOf(item);
        if (index > -1) {
          this.multiValue.splice(index, 1);
        } else {
          this.multiValue.push(item);
        }
        this.selectValue = this.multiValue.sort().join("");
      } else {
        this.selectValue = item;
      }
      this.backValue();
    },
    editorChange (value) {
      this.selectValue = value;
      this.backValue();
    },
    backValue () {
      this.$emit("change", this.selectValue);
    }
  },
  computed: {
    // eslint-disable-next-line vue/return-in-computed-property
    selectOptions () {
      if (this.questionType === 1) {
        return newsetDesc(this.questionOption);
      } else if (this.questionType === 2) {
        return newsetDesc(this.questionOption);
      } else if (this.questionType === 3) {
        return this.judgeChoice;
      }
    }
  },
  watch: {
    answerValue (newVal, oldVal) {
      if (newVal !== this.selectValue) {
        this.selectValue = newVal;
        this.multiValue = this.selectValue ? this.selectValue.split("") : [];
      }
    }
  }
};
</script>

