<template>
  <div class="sckviewer">
    <Calendar
      @Changed="DateChanged"
      :ScheduledDate.sync="MaterialDate"
      :ChooseDate.sync="ChooseDate"
    />
    <p class="title">{{FormatDate(ChooseDate)}}录播资源</p>
    <div class="lists" v-if="materials">
      <ul>
        <li v-for="(item,index) in materials" :key="index">
          <div>
            <span>{{item.RTStart}}</span>
            <span>~</span>
            <span>{{item.RTEnd}}</span>
          </div>
          <div>
            <p>
              <span>{{item.RTName}}</span>
              <span>{{item.Lesson}}</span>
            </p>
            <p class="list_tcgrid">
              <span>教师：{{item.Teachers}}</span>
              <span>班级：{{item.Classes}}</span>
            </p>
            <p>
              <span>
                视频源：
                <el-dropdown
                  @command="Play"
                  v-for="(video,index) in item.videos"
                  :key="index"
                  placement="bottom"
                  class="video_source"
                >
                  <span class="el-dropdown-link">
                    {{video.title}}
                    <i class="el-icon-arrow-down el-icon--right"></i>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item
                      :command="vs"
                      v-for="(vs,index) in video.videoSource"
                      :key="index"
                    >{{vs.MaterialName}}</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              </span>
              <span>
                <el-dropdown @command="Download" class="video_download">
                  <span class="el-dropdown-link">
                    下载
                    <i class="el-icon-arrow-down el-icon--right"></i>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item
                      :command="dl"
                      v-for="(dl,index) in item.downloads"
                      :key="index"
                    >{{dl.MaterialName}}</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              </span>
            </p>
          </div>
        </li>
      </ul>
    </div>
    <div v-if="!materials" class="none">
      <p>{{FormatMonthDay(ChooseDate)}} 无资源</p>
    </div>
    <el-dialog
      custom-class="dlbPlayer"
      :modal="false"
      title="播放"
      :visible.sync="ShowPlayer"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
      :before-close="OnDlgPlayerClose"
      :destroy-on-close="true"
      center
    >
      <PlayBox ref="PlayBox" :Videos.sync="videoList" />
    </el-dialog>
  </div>
</template>

<script>
import moment from "moment";
import api from "@/scripts/api";
import store from "@/scripts/store";
export default {
  components: {
    Calendar: () => import("@/components/Calendar"),
    PlayBox: () => import("@/components/Player/PlayBox")
  },
  props: {},
  data() {
    return {
      MaterialDate: [],
      ChooseDate: new Date(),
      videoList: [],
      ShowPlayer: false,
      materials: []
    };
  },
  mounted() {
    var sDate = this.MonthHeadDate(this.ChooseDate);
    var eDate = this.MonthEndDate(this.ChooseDate);
    var sLogDate = this.DayHeadDate(this.ChooseDate);
    var eLogDate = this.DayEndDate(this.ChooseDate);
    this.LoadData(sDate, eDate, sLogDate, eLogDate);
  },
  methods: {
    FormatDate(day) {
      return moment(day).format("YYYY-MM-DD");
    },
    FormatMonthDay(day) {
      return moment(day).format("MM-DD");
    },
    MonthHeadDate(date) {
      return moment(date).format("YYYY-MM") + "-01 00:00:00";
    },
    MonthEndDate(date) {
      return (
        moment(date).format("Y-MM") +
        "-" +
        moment(moment(date).format("Y-MM"), "Y-MM").daysInMonth() +
        " 23:59:59"
      );
    },
    DayHeadDate(date) {
      return moment(date).format("YYYY-MM-DD") + " 00:00:00";
    },
    DayEndDate(date) {
      return moment(date).format("YYYY-MM-DD") + " 23:59:59";
    },
    DateChanged(date) {
      var sDate = this.MonthHeadDate(date);
      var eDate = this.MonthEndDate(date);
      var sLogDate = this.DayHeadDate(date);
      var eLogDate = this.DayEndDate(date);
      this.LoadData(sDate, eDate, sLogDate, eLogDate);
    },
    async LoadData(sDate, eDate, sLogDate, eLogDate) {
      const roomid = this.$parent.$route.params.id;
      const res = await api.Material.GetMaterialByDate(
        roomid,
        sDate,
        eDate,
        sLogDate,
        eLogDate
      );
      this.MaterialDate = [];
      this.materials = null;
      if (res.Result) {
        if (res.Dates && res.Dates.length > 0) {
          res.Dates.forEach(element => {
            this.MaterialDate.push(element.Time);
          });
        }
        if (res.Datas && res.Datas.length > 0) {
          this.materials = [];

          res.Datas.forEach(element => {
            var materialInfo = null;
            var scheduleInfo = null;
            var videos = [];
            var videoTeach = null;
            var videoStu = null;
            var videoCard = null;

            if (element.MaterialInfo) {
              materialInfo = JSON.parse(element.MaterialInfo);
            }
            if (element.ScheduleInfo) {
              scheduleInfo = JSON.parse(element.ScheduleInfo);
            }

            var vT = materialInfo.filter(m => m.FrameType === "2");
            if (vT.length > 0) {
              videoTeach = { title: "老师", videoSource: [] };
              vT.forEach(ele => {
                videoTeach.videoSource.push(ele);
              });
              videos.push(videoTeach);
            }
            var vS = materialInfo.filter(m => m.FrameType === "1");
            if (vS.length > 0) {
              videoStu = { title: "学生", videoSource: [] };
              vS.forEach(ele => {
                videoStu.videoSource.push(ele);
              });
              videos.push(videoStu);
            }
            var vC = materialInfo.filter(m => m.FrameType === "0");
            if (vC.length > 0) {
              videoCard = { title: "课件", videoSource: [] };
              vC.forEach(ele => {
                videoCard.videoSource.push(ele);
              });
              videos.push(videoCard);
            }

            this.materials.push({
              RTStart: materialInfo
                ? moment(materialInfo[0].RTStart).format("HH:mm:ss")
                : "",
              RTEnd: materialInfo
                ? moment(materialInfo[0].RTEnd).format("HH:mm:ss")
                : "",
              RTName: element.RTName,
              Lesson: scheduleInfo ? scheduleInfo.Lesson : "",
              Teachers: scheduleInfo ? scheduleInfo.Teachers : "",
              Classes: scheduleInfo ? scheduleInfo.Classes : "",
              videos,
              downloads: materialInfo
            });
          });
        }
      } else {
        this.$message({
          showClose: true,
          message: res.Info,
          type: "error",
          duration: 1000
        });
      }
    },
    OnDlgPlayerClose() {
      this.videoList = [];
      if (this.$refs.PlayBox) {
        this.$refs.PlayBox.Dispose();
      }
      this.$nextTick(() => {
        this.ShowPlayer = false;
      });
    },
    Play(row) {
      this.videoList = [];
      this.$nextTick(async () => {
        if (row.RTID) {
          this.videoList = await this.GetResourceList(row);
        } else {
          const imgSrc =
            this.$root.DownloadURL +
            "?filename=" +
            row.MaterialID +
            "&fileType=9";
          if (row.Type === "MP4") {
            this.videoList.push({
              url: imgSrc,
              id: row.MaterialID,
              name: row.MaterialName,
              MaterialStorePath: row.Path
            });
          }
        }
        this.ShowPlayer = true;
      });
    },
    // 获取视频资源
    async GetResourceList(row) {
      const videoList = [];
      if (row.MaterialInfo) {
        await row.MaterialInfo.forEach(ele => {
          const imgSrc =
            this.$root.DownloadURL +
            "?filename=" +
            ele.MaterialID +
            "&fileType=9";

          if (ele.Type === "MP4") {
            videoList.push({
              url: imgSrc,
              id: ele.MaterialID,
              name: ele.MaterialName,
              MaterialStorePath: ele.Path
            });
          }
        });
      }
      return videoList;
    },
    async Download(row) {
      const ConfigInfo = store.GetConfigInfo();
      const FileSaver = require("file-saver");

      if (row) {
        if (row.RTID) {
          const videos = await this.GetResourceList(row);

          videos.forEach(video => {
            FileSaver.saveAs(
              `http://${ConfigInfo.Host}:${ConfigInfo.Port}/FJRH.Campus/MaterialService/Download?mid=${video.id}`
            );
          });
        } else {
          FileSaver.saveAs(
            `http://${ConfigInfo.Host}:${ConfigInfo.Port}/FJRH.Campus/MaterialService/Download?mid=${row.MaterialID}`
          );
        }
      }
    }
  }
};
</script>

<style lang="less">
.sckviewer {
  .none {
    > p {
      font-size: 48px;
      text-align: center;
      line-height: 300px;
      color: #c7cdd3;
    }
  }
  .list_tcgrid {
    display: grid !important;
    grid-template-columns: 1fr 1fr;
  }
  .video_source {
    color: @ThemeColor;
    margin-right: 20px;
    > span {
      cursor: pointer;
      > i {
        cursor: pointer;
        margin: 0px;
        font-size: 12px;
      }
    }
  }
  .video_download {
    color: #fff;
    display: inline-block;
    background-color: @ThemeColor;
    border-radius: 3px;
    width: 50px;
    text-align: center;
    > span {
      cursor: pointer;
      > i {
        cursor: pointer;
        margin: 0px;
        font-size: 12px;
      }
    }
  }
}
</style>