<template>
  <div class="clsroom-list">
    <el-card shadow="hover" class="living-plan">
      <div class="info">
        <span class="title">正在播放的计划</span>
        <el-divider direction="vertical"></el-divider>
        <span class="desc">这是一条正在播放中的计划，它已经播放很久了</span>
      </div>
      <el-select
        v-model="immValue"
        @change="addFile"
        size="mini"
        class="select-imm"
        placeholder="即时广播"
      >
        <el-option
          v-for="item in immOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
    </el-card>
    <el-card v-if="plans.length !== 0" shadow="hover" class="imm-file">
      <div class="title">即时广播文件 {{ plans.length }} 条</div>
      <div class="infinite-list" style="overflow: auto">
        <div class="con" v-for="item in plans" :key="item.value">
          <div class="info">
            <div class="detail">
              <div class="file-name">
                <span>计划名称：{{ item.planName }}</span>
                <span>文件名称：{{ item.fileName }}</span>
                <span>类型：{{ item.fileType }}</span>
                <span>文件大小：{{ item.fileSize }}</span>
              </div>
            </div>
          </div>
          <div class="op">
            <span class="tip-text">请勾选要插播的教室</span>
            <!-- <el-button type="primary" size="mini">点击预览</el-button> -->
            <el-button
              v-if="item.planEstate == 0"
              @click="CutIn(item)"
              type="primary"
              size="mini"
              >插播</el-button
            >
            <el-button
              v-if="item.planEstate == 1"
              @click="StopBroad(item)"
              type="primary"
              size="mini"
              >停止</el-button
            >
            <el-button
              v-if="item.planEstate == 0"
              @click="DelPlan(item)"
              type="danger"
              size="mini"
              >删除</el-button
            >
          </div>
        </div>
      </div>
    </el-card>
    <div class="el-page-header" ref="divHeader">
      您的权限共教室
      <span>{{ TotalCount }}</span> 间
    </div>
    <FilterBar
      ref="FilterBar"
      :FilterItems.sync="FilterItems"
      @Filter="Filter"
      @ReSize="AdjustSize"
    />
    <el-card ref="CountBar" shadow="hover" class="countbar">
      <el-radio-group size="mini" v-model="CurEstate" @change="ReSelections">
        <el-radio-button label="all">
          筛选结果共
          <span class="free">{{ FilterCount }}</span>
          间
        </el-radio-button>
        <el-radio-button label="hasCls" v-if="!IsMINI">
          有排课
          <span class="hasCls">{{ ScheduleCount }}</span>
          间
        </el-radio-button>
        <el-radio-button label="noCls" v-if="!IsMINI">
          未排课
          <span class="online">{{ UnScheduleCount }}</span>
          间
        </el-radio-button>
        <!-- <el-radio-button label="terminal" v-if="!IsMINI">
          有终端
          <span class="online">{{TerminalCount}}</span>
          间
        </el-radio-button> -->
        <el-radio-button label="using">
          终端使用中
          <span class="using">{{ UsingCount }}</span>
          间
        </el-radio-button>
        <el-radio-button label="free">
          终端空闲
          <span class="online">{{ FreeCount }}</span>
          间
        </el-radio-button>
        <el-radio-button label="offline">
          终端离线
          <span class="offline">{{ OfflineCount }}</span>
          间
        </el-radio-button>
      </el-radio-group>
      <el-button-group>
        <el-tooltip placement="top" content="全部选择">
          <el-button
            plain
            type="primary"
            size="mini"
            icon="iconfont icon_SelectAll"
            @click="CheckALL"
            >全选</el-button
          >
        </el-tooltip>
        <el-tooltip placement="top" content="反向选择">
          <el-button
            plain
            type="primary"
            icon="iconfont icon_ReverseSelect"
            size="mini"
            @click="ReverseCheck"
            >反选</el-button
          >
        </el-tooltip>
        <el-tooltip placement="top" content="取消选择">
          <el-button
            plain
            type="primary"
            icon="iconfont icon_CancelSelect"
            size="mini"
            @click="UnCheckALL"
            >取消</el-button
          >
        </el-tooltip>
      </el-button-group>
    </el-card>
    <el-checkbox-group
      class="rooms"
      :class="'rooms_' + divRoomsCount"
      ref="divRooms"
      v-model="Selections"
      :style="{ height: newHeight + 'px' }"
    >
      <el-tooltip
        class="item"
        placement="top"
        v-for="item in Rooms"
        :key="item.ClsRoomID"
        :disabled="true"
      >
        <el-card
          :class="{
            hasCls: item.hasCls,
            rec: item.Rec === 1,
            live: item.Live === 1,
          }"
          shadow="hover"
          @click.native="CardClick(item.ClsRoomID)"
          @dblclick.native="GoDetail(item.ClsRoomID)"
          v-if="
            (FilterBuilds.length <= 0 ||
              FilterBuilds.indexOf(item.BuildingID) >= 0) &&
            (!FilterClsRoomType || FilterClsRoomType === item.ClsRoomType) &&
            (CurEstate === 'all' ||
              (CurEstate === 'hasCls' && item.hasCls) ||
              (CurEstate === 'noCls' && !item.hasCls) ||
              (CurEstate === 'terminal' && item.Terminal) ||
              (CurEstate === 'using' &&
                item.Terminal &&
                item.Terminal.State === 3) ||
              (CurEstate === 'free' &&
                item.Terminal &&
                (item.Terminal.State === 0 ||
                  item.Terminal.State === 1 ||
                  item.Terminal.State === 2)) ||
              (CurEstate === 'offline' &&
                item.Terminal &&
                item.Terminal.State === -1))
          "
        >
          <div class="title">
            <el-checkbox
              :ref="'chk_' + item.ClsRoomID"
              :label="item.ClsRoomID"
              >{{ item.ClsRoomName }}</el-checkbox
            >
            <div class="Mask"></div>
          </div>
          <div class="info">
            <span>IP地址：{{ item.Terminal.IP }}</span>
            <span>广播中：{{ item.IPBName }}</span>
          </div>
          <div class="content status">
            <el-badge
              is-dot
              class="item"
              :hidden="!item.Terminal || !item.Terminal.DifferIP"
            >
              <i
                class="iconfont icon_terminal"
                :class="{
                  offline: !item.Terminal || item.Terminal.State === -1,
                  using: item.Terminal && item.Terminal.State === 3,
                  ShuttingDown: item.Terminal && item.Terminal.State === 1,
                  Opening: item.Terminal && item.Terminal.State === 2,
                  free: item.Terminal && item.Terminal.State === 0,
                }"
              />
            </el-badge>
            <i
              class="iconfont icon_broadcast"
              :class="{
                offline: !item.IPBState,
                live: item.IPBState,
              }"
            />
          </div>
        </el-card>
      </el-tooltip>
    </el-checkbox-group>
  </div>
</template>

<script>
// import $ from "jquery";
import api from "@/scripts/api";
export default {
  components: {
    FilterBar: () => import("@/components/Filter"),
  },
  created() {
    this.$parent.$on("FixArea", (data, node) => {
      if (node.level === 1) {
        const ids = [];
        if (data.Children) {
          data.Children.forEach((element) => {
            ids.push(element.id);
          });
        }
        this.FilterItems[1].Values = ids;
      } else {
        this.FilterItems[1].Values = [data.id];
      }

      const LZString = require("lz-string");
      const jsonStr = JSON.stringify(this.FilterItems[1].Values);
      this.Filter(
        "ClsRoom",
        LZString.compressToBase64(jsonStr.slice(1, jsonStr.length - 1))
      );
    });
  },
  beforeDestroy() {
    window.onresize = null;
    if (window.hub) {
      window.hub.off("UpdateTimeUp", null);
      window.hub.off("ReceiveStatus", null);
      window.hub.off("ReceiveRBStatus", null);
      window.hub.off("UpdateGWLineState", null);
      window.hub.off("ReceiveIPBInfo", null);
      window.hub.off("ReceiveIPBImmediate", null);
      console.log("释放监听");
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.AdjustSize();
    });
    window.onresize = () => {
      this.AdjustSize();
    };
    this.LoadData();
    this.LoadImmData();

    this.ListenTimer = window.setInterval(() => {
      this.Listen();
    }, 1000);
  },
  data() {
    return {
      IsHubOn: false,
      IsMINI: false,
      CurEstate: "all",
      divRoomsCount: 8,
      FilterCount: 0,
      ScheduleCount: 0,
      UnScheduleCount: 0,
      TerminalCount: 0,
      UsingCount: 0,
      FreeCount: 0,
      OfflineCount: 0,
      TotalCount: 0,
      immValue: "",
      immOptions: [
        {
          label: "视频",
          value: "video",
        },
        {
          label: "音频",
          value: "audio",
        },
        {
          label: "文本",
          value: "text",
        },
        {
          label: "文档",
          value: "doc",
        },
        {
          label: "直播",
          value: "live",
        },
      ],
      FilterBuilds: [],
      FilterClsRoomType: "",
      FilterItems: [
        {
          Type: "ClsRoomType",
          ShowMore: false,
          Multiple: false,
        },
        {
          Type: "ClsRoom",
          TypeName: "教室区域",
          Level: 2,
          Values: [],
          Multiple: true,
        },
      ],
      newHeight: 0,
      Rooms: [],
      Selections: [],
      ListenTimer: null,
      plans: [
        {
          name: "123123",
          type: "123123",
          size: "123123",
        },
        {
          name: "123123",
          type: "123123",
          size: "123123",
        },
        {
          name: "123123",
          type: "123123",
          size: "123123",
        },
      ],
    };
  },
  methods: {
    Listen() {
      if (!this.IsHubOn) {
        if (window.hub) {
          this.IsHubOn = true;
          const $this = this;

          window.hub.on("UpdateTimeUp", function (jsonInfo) {
            $this.GetScheduleDatas();
          });

          window.hub.on("ReceiveIPBInfo", function (jsonInfo) {
            $this.UpdateIPBData(jsonInfo);
          });

          window.hub.on("ReceiveIPBImmediate", function (jsonInfo) {
            $this.LoadImmData();
          });

          window.hub.on("ReceiveStatus", function (serialNo, jsonInfo) {
            const findR = $this.Rooms.find(
              (r) =>
                r.Terminal !== null &&
                (r.Terminal.DeviceSerialNo === serialNo ||
                  r.ClsRoomID === serialNo)
            );

            if (findR != null) {
              var terminalInfo = JSON.parse(jsonInfo);
              if (terminalInfo.IP !== findR.Terminal.DeviceInfo.IP) {
                findR.Terminal.DifferIP = true;
              }

              switch (terminalInfo.State) {
                case "Opened": {
                  findR.Terminal.State = 3;
                  break;
                }
                case "Opening": {
                  findR.Terminal.State = 2;
                  break;
                }
                case "ShuttingDown": {
                  findR.Terminal.State = 1;
                  break;
                }
                case "Shutdown": {
                  findR.Terminal.State = 0;
                  break;
                }
                case "OffLine": {
                  findR.Terminal.State = -1;
                  break;
                }
              }
            }
            $this.StatisticalCount();
          });

          window.hub.on("ReceiveRBStatus", function (jsonInfo) {
            if ($this.Rooms) {
              var recInfo = JSON.parse(jsonInfo);
              $this.Rooms.forEach((room) => {
                if (room.ClsRoomID === recInfo.ClsRoomID) {
                  room.RB.REC = recInfo.RECEstate;
                  // room.Live = data.LiveEstate;
                }
              });
            }
            $this.StatisticalCount();
          });

          window.hub.on("UpdateGWLineState", function (jsonInfo) {
            if (jsonInfo && jsonInfo.ClsRoomID) {
              if ($this.Rooms) {
                $this.Rooms.forEach((room) => {
                  if (room.ClsRoomID === jsonInfo.ClsRoomID) {
                    room.IOTState = jsonInfo.State ? 1 : 0;
                  }
                });
              }
            }
          });
        }
      }
    },
    // 取消选择
    UnCheckALL() {
      this.Selections = [];
    },
    // 反选
    ReverseCheck() {
      this.Rooms.forEach((r) => {
        const i = this.Selections.indexOf(r.ClsRoomID);
        if (i < 0) {
          this.Selections.push(r.ClsRoomID);
        } else {
          this.Selections.splice(i, 1);
        }
      });

      this.ReSelections();
    },
    // 全选
    CheckALL() {
      this.Rooms.forEach((r) => {
        if (this.Selections.indexOf(r.ClsRoomID) < 0) {
          this.Selections.push(r.ClsRoomID);
        }
      });

      this.ReSelections();
    },
    // 单击卡片 切换选中状态
    CardClick(id) {
      const i = this.Selections.indexOf(id);
      if (i >= 0) {
        this.Selections.splice(i, 1);
      } else {
        this.Selections.push(id);
      }
    },
    // 双击卡片
    GoDetail(id) {
      this.$router.push(`/control/dtl/${id}`);
    },
    // 筛选
    Filter(type, val) {
      switch (type) {
        case "ClsRoom": {
          const LZString = require("lz-string");
          const ids = `[${LZString.decompressFromBase64(val)}]`;
          this.FilterBuilds = JSON.parse(ids);
          break;
        }
        case "ClsRoomType": {
          this.FilterClsRoomType = val;
          break;
        }
      }
      this.ReSelections();
      this.StatisticalCount();
    },
    // 统计个数
    StatisticalCount() {
      this.FilterCount = this.Rooms.filter(
        (r) =>
          (this.FilterBuilds.length <= 0 ||
            this.FilterBuilds.indexOf(r.BuildingID) >= 0) &&
          (!this.FilterClsRoomType || this.FilterClsRoomType === r.ClsRoomType)
      ).length;

      this.ScheduleCount = this.Rooms.filter(
        (r) =>
          (this.FilterBuilds.length <= 0 ||
            this.FilterBuilds.indexOf(r.BuildingID) >= 0) &&
          (!this.FilterClsRoomType ||
            this.FilterClsRoomType === r.ClsRoomType) &&
          r.hasCls
      ).length;
      this.TerminalCount = this.Rooms.filter(
        (r) =>
          (this.FilterBuilds.length <= 0 ||
            this.FilterBuilds.indexOf(r.BuildingID) >= 0) &&
          (!this.FilterClsRoomType ||
            this.FilterClsRoomType === r.ClsRoomType) &&
          r.Terminal
      ).length;
    },
    // 筛选过后重新选择
    ReSelections() {
      for (let i = this.Selections.length - 1; i >= 0; i--) {
        const findR = this.Rooms.find(
          (r) => r.ClsRoomID === this.Selections[i]
        );
        if (findR) {
          if (
            (this.FilterBuilds.length <= 0 ||
              this.FilterBuilds.indexOf(findR.BuildingID) >= 0) &&
            (!this.FilterClsRoomType ||
              this.FilterClsRoomType === findR.ClsRoomType) &&
            (this.CurEstate === "all" ||
              (this.CurEstate === "hasCls" && findR.hasCls) ||
              (this.CurEstate === "noCls" && !findR.hasCls) ||
              (this.CurEstate === "terminal" && findR.Terminal) ||
              (this.CurEstate === "using" &&
                findR.Terminal &&
                findR.Terminal.State === 3) ||
              (this.CurEstate === "free" &&
                findR.Terminal &&
                (findR.Terminal.State === 0 ||
                  findR.Terminal.State === 1 ||
                  findR.Terminal.State === 2)) ||
              (this.CurEstate === "offline" &&
                findR.Terminal &&
                findR.Terminal.State === -1))
          ) {
          } else {
            this.Selections.splice(i, 1);
          }
        } else {
          this.Selections.splice(i, 1);
        }
      }
    },
    // 加载数据
    async LoadData() {
      this.TotalCount = 0;
      this.Rooms = [];
      const res = await api.ClsRoom.GetAllIPBRooms();
      if (res && res.Datas) {
        res.Datas.forEach((room) => {
          const deviceInfo = room.DeviceInfo
            ? JSON.parse(room.DeviceInfo)
            : null;

          let terminal = null;

          if (deviceInfo) {
            terminal = deviceInfo.find((d) => d.DeviceType === "1");
          }

          this.Rooms.push({
            ClsRoomID: room.ClsRoomInfo.ClsRoomID,
            ClsRoomName: room.ClsRoomInfo.ClsRoomName,
            BuildingID: room.ClsRoomInfo.BuildingID,
            ClsRoomType: room.ClsRoomInfo.ClsRoomType,
            IPBName: "",
            IPBState: false,
            Terminal: terminal
              ? {
                  State: -1,
                  DeviceSerialNo: terminal.DeviceSerialNo,
                  ID: terminal.id,
                  DeviceInfo: terminal.DeviceInfo,
                  IP: terminal.DeviceInfo.IP,
                  Port: terminal.DeviceInfo.Port,
                  ModelCode: terminal.DeviceInfo.ModelCode,
                  DifferIP: false,
                }
              : null,
          });
        });

        if (res.RBInfos) {
          res.RBInfos.forEach((element) => {
            this.Rooms.forEach((room) => {
              if (room.ClsRoomID === element.ClsRoomID) {
                room.RB.REC = element.RECEstate;
              }
            });
          });
        }

        this.TotalCount = this.Rooms.length;

        const resState = await api.Ctrl.AllCtrlStates();
        if (resState) {
          resState.Datas.forEach((ti) => {
            const findR = this.Rooms.find(
              (r) =>
                r.ClsRoomID === ti.ClsRoomID ||
                (r.Terminal &&
                  (r.Terminal.DeviceSerialNo === ti.SerialNo ||
                    r.Terminal.DeviceInfo.IP === ti.IP))
            );

            if (findR) {
              if (!findR.Terminal) {
                findR.Terminal = {
                  State: -1,
                  DeviceSerialNo: ti.SerialNo,
                  DeviceInfo: {
                    IP: ti.IP,
                    Port: ti.Port,
                    ModelCode: ti.Model,
                    BrandCode: ti.Brand,
                  },
                  DifferIP: false,
                };
              }

              if (findR) {
                switch (ti.State) {
                  case "Opened": {
                    findR.Terminal.State = 3;
                    break;
                  }
                  case "Opening": {
                    findR.Terminal.State = 2;
                    break;
                  }
                  case "ShuttingDown": {
                    findR.Terminal.State = 1;
                    break;
                  }
                  case "Shutdown": {
                    findR.Terminal.State = 0;
                    break;
                  }
                  case "OffLine": {
                    findR.Terminal.State = -1;
                    break;
                  }
                }
              }
            }
          });
        }
      }

      if (res && res.IPBStatusInfo) {
        this.UpdateIPBData(res.IPBStatusInfo);
      }

      this.GetScheduleDatas();
      this.StatisticalCount();
    },
    async LoadImmData() {
      const res = await api.BP.GetImmediateDatas();
      this.plans = [];
      if (res && res.Datas) {
        res.Datas.forEach((ele) => {
          var fileType = "";
          var fileSize = 0;
          if (ele.MaterialFileSize) {
            fileSize = (parseInt(ele.MaterialFileSize) / 1024 / 1024).toFixed(
              2
            );
          }
          switch (ele.BPLType) {
            case "1":
              fileType = "视频文件";
              break;
            case "2":
              fileType = "音频文件";
              break;
            case "3":
              fileType = "文本";
              break;
            case "4":
              fileType = "文档文件";
              break;
            case "6":
              fileType = "直播";
              break;
          }
          this.plans.push({
            planName: ele.BPLName,
            fileName: ele.MaterialName,
            fileType,
            fileSize: fileSize + " M",
            planEstate: ele.Key.BPEstate,
            bpid: ele.Key.BPID,
          });
        });
      }
    },
    // 调整
    AdjustSize() {
      const paneWrap = document.querySelector(".plan-tab .el-tabs__content");
      const wrapHeight = parseInt(window.getComputedStyle(paneWrap).height);

      const headerHeight =
        parseInt(window.getComputedStyle(this.$refs.divHeader).height) + 16 * 2;

      const filterHeight = this.$refs.FilterBar
        ? parseInt(window.getComputedStyle(this.$refs.FilterBar.$el).height)
        : 0;

      const countBarHeight =
        parseInt(window.getComputedStyle(this.$refs.CountBar.$el).height) +
        16 * 2;
      this.newHeight =
        wrapHeight -
        headerHeight -
        (filterHeight > 0 ? filterHeight - 8 : -16) -
        countBarHeight;

      // console.log(
      //   wrapHeight,
      //   headerHeight,
      //   filterHeight,
      //   countBarHeight,
      //   this.newHeight
      // );

      this.divRoomsCount = Math.floor(
        parseInt(window.getComputedStyle(this.$refs.divRooms.$el).width) / 200
      );

      const width = parseInt(window.innerWidth);

      if (width < 1920) {
        this.IsMINI = true;
      } else {
        this.IsMINI = false;
      }
    },
    // 获取当前课表信息
    async GetScheduleDatas() {
      const res = await api.Schedule.GetCurrentDatas();
      this.Rooms.forEach((room) => {
        room.hasCls = false;
        room.Teachers = "";
        room.CourseName = "";
        room.Classes = "";
        if (res) {
          const find = res.Datas.find(
            (d) => d.Key.ClsRoomID === room.ClsRoomID
          );
          if (find) {
            room.hasCls = true;

            const thcs = find.ThcINFO ? JSON.parse(find.ThcINFO) : [];
            if (thcs) {
              thcs.forEach((t, i) => {
                room.Teachers += t.TeacherName;
                if (i < thcs.length - 1) room.Teachers += "，";
              });
            }

            room.CourseName = find.CourseName;

            const clses = find.ClassInfo ? JSON.parse(find.ClassInfo) : [];
            if (clses) {
              clses.forEach((c, i) => {
                room.Classes += c.ClassName;
                if (i < clses.length - 1) room.Classes += "，";
              });
            }
          }
        }
      });
      this.StatisticalCount();
    },
    UpdateIPBData(info) {
      if (info) {
        var datas = JSON.parse(info);
        this.Rooms.forEach((ele) => {
          ele.IPBState = false;
          ele.IPBName = "";
          var find = datas.find((d) => d.IP === ele.Terminal.IP);
          if (find) {
            ele.IPBState = find.Online;
            if (find.Status === 1) {
              ele.IPBName = find.Name;
            }
          }
        });
      }
    },
    addFile() {
      this.$router.push(`/ipb/file/add/${this.immValue}/1`);
    },
    async CutIn(obj) {
      if (this.Selections.length > 0) {
        var ids = "";
        for (let i = this.Selections.length - 1; i >= 0; i--) {
          const findR = this.Rooms.find(
            (r) => r.ClsRoomID === this.Selections[i]
          );
          if (findR) {
            if (findR.Terminal) {
              ids += findR.Terminal.ID + ",";
            }
            this.Selections.splice(i, 1);
          } else {
            this.Selections.splice(i, 1);
          }
        }
        const LZString = require("lz-string");
        if (ids) {
          ids = LZString.compressToBase64(ids.slice(0, ids.length - 1));
        }
        const res = await api.BP.CutInIPB(obj.bpid, ids);
        if (res && res.Result) {
          this.LoadImmData();
        } else {
          var msg = "";
          if (res.Info) {
            res.Info.forEach((element) => {
              msg += element + "\r\n";
            });
          } else {
            msg = "插播失败";
          }
          this.$message({
            message: msg,
            type: "warning",
            duration: 1000,
          });
        }
      } else {
        this.$message({
          message: "请选择插播的教室",
          type: "warning",
          duration: 1000,
        });
      }
    },
    async StopBroad(obj) {
      const res = await api.BP.StopImmediate(obj.bpid);
      if (res && res.Result) {
        this.LoadImmData();
      } else {
        var msg = "";
        if (res.Info) {
          res.Info.forEach((element) => {
            msg += element + "\r\n";
          });
        } else {
          msg = "停止失败";
        }
        this.$message({
          message: msg,
          type: "warning",
          duration: 1000,
        });
      }
    },
    async DelPlan(obj) {
      await api.BP.Delete(obj.bpid);
      this.LoadImmData();
    },
  },
};
</script>

