<template>
  <el-drawer
    title="评价详情"
    :visible.sync="visible"
    :append-to-body="true"
    custom-class="eva-list"
    size="800px"
    direction="rtl"
    @close="Close"
  >
    <div name="title">
      <span>教师名称：{{ data.TeacherName }}</span>
      <span>课程名称：{{ data.CourseName }}</span>
      <div style="float:right" v-if="mCode == 'ReportStatistics'">
        <span>综合得分：{{ this.avgScore }}</span>
      </div>
    </div>
    <Table
      :ModuleCode="mCode"
      :ShowExport="false"
      :ShowCreate="false"
      KeyFileld="RFID"
      :Columns="Columns"
      :SortField.sync="SortField"
      :SortDir.sync="SortDir"
      :Datas.sync="Datas"
      :PageIndex.sync="PageIndex"
      :PageSize.sync="PageSize"
      :TotalCounts="TotalCounts"
      :BaseForm.sync="BaseForm"
      :SearchKey.sync="SKey"
      :SearchValue.sync="Key"
      :UnNeedEstateBtn="true"
      @LoadData="LoadData"
      :UnNeedDelBtn="true"
      :ShowSelectColumn="false"
    >
      <template slot-scope="scope" slot="oprateA">
        <el-button
          class="btnViewData"
          onfocus="this.blur()"
          type="primary"
          icon="el-icon-document"
          size="mini"
          plain
          @click="Detail(scope.row)"
          >详情</el-button
        >
      </template>
    </Table>
  </el-drawer>
</template>
<script>
import api from "@/scripts/api";
export default {
  props: {
    data: {},
    show: {
      type: Boolean,
      default: false
    },
    mCode: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      Loading: false,
      visible: false,
      Columns: [
        {
          Code: "UserName",
          Header: "评价人"
        },
        {
          Code: "typeName",
          Header: "评价人角色"
        },
        {
          Code: "RFCreateDate",
          Header: "评价时间"
        },
        {
          Code: "score",
          Header: "分数",
          Width: "60"
        },
        {
          Code: "oprateA",
          Header: "操作",
          Width: "120",
          Slot: true
        }
      ],
      Datas: [],
      PageIndex: 1,
      PageSize: 10,
      TotalCounts: 0,
      Key: "",
      SKey: "",
      SortField: "",
      SortDir: 0,
      Estate: "",
      BaseForm: {},
      id: "",
      avgScore: 0
    };
  },
  components: {
    Table: () => import("@/components/Table")
  },
  mounted() {
    if (this.mCode === "ReportStatistics") {
      this.Columns.splice(
        4,
        0,
        {
          Code: "fullScore",
          Header: "满分值",
          Width: "60"
        },
        {
          Code: "line",
          Header: "合格线",
          Width: "60"
        }
      );
    }
  },
  watch: {
    show(val) {
      this.visible = val;
      if (val) this.LoadData();
    }
  },
  methods: {
    Detail(r) {
      console.log("aaa");
      // this.$emit("Detail", r);
      this.$router.push(
        `/eval/dtl/0/${r.EVALPID}/${r.RFID}/${r.CSID}/${r.GaugeID}`
      );
    },
    Close() {
      this.PageIndex = 1;
      this.PageSize = 20;
      this.$emit("update:show", false);
    },
    async LoadData() {
      let res = "";
      if (this.mCode === "ReportStatistics") {
        res = await api.EVALPlan.GetExeOfEPlanDetailByCourseID(
          this.data.CourseID,
          this.PageIndex,
          this.PageSize,
          "",
          0
        );
      } else {
        res = await api.EVALPlan.GetExecutionOfEPlanDetailByID(
          this.data.EVALPID,
          -1,
          this.PageIndex,
          this.PageSize,
          "",
          0
        );
      }
      this.avgScore = 0;
      if (res && res.Datas && res.Datas.length > 0) {
        res.Datas.forEach(ele => {
          ele.score = Number(ele.score).toFixed(2);
          ele.usertype === "2"
            ? (ele.typeName = "学生")
            : (ele.typeName = "老师");
          this.avgScore = this.avgScore + Number(ele.score);
          if (this.mCode === "ReportStatistics") {
            ele.fullScore = Number(ele.fullScore).toFixed(2);
            ele.line = Number(ele.line).toFixed(2);
          }
        });
        this.avgScore = (this.avgScore / res.Datas.length).toFixed(2);
        this.Datas = res.Datas;
        this.TotalCounts = res.rowsCount;
      } else {
        this.Datas = [];
        this.TotalCounts = 0;
      }
    }
  }
};
</script>
<style lang="less">
.eva-list {
  .tbl {
    width: 760px;
    margin: 0 auto;
  }
  .el-table__body-wrapper {
    max-height: calc(100vh - 210px) !important;
    overflow-y: auto;
  }
  .el-drawer__header {
    border-bottom: 1px solid #eee;
    margin-bottom: 16px;
    font-size: 18px;
    color: #333;
  }
  .tbl .handle_bar {
    display: none;
  }
  .el-table .el-table__header thead tr th {
    padding: 6px 5px;
  }
  .el-table .el-table__body tbody tr td {
    padding: 7px;
  }
  div[name="title"] span {
    font-size: 15px;
    color: #333;
    margin-right: 20px;
  }
  div[name="title"] {
    margin-bottom: 12px;
    margin-left: 20px;
  }
}
</style>
