<template>
  <el-tooltip content="输入教师关键字可以进行模糊筛选" :placement="TooltipPlacement">
    <el-select
      :size="size"
      popper-class="selTeachers"
      filterable
      :allow-create="ArrowCreate"
      :multiple="multiple"
      :multiple-limit="5"
      v-model="Value"
      :placeholder="Placeholder"
      collapse-tags
      @change="ChangeHandle"
      remote
      clearable
      :remote-method="remoteMethod"
      reserve-keyword
      :loading="loading"
    >
      <el-option
        v-for="item in Datas"
        :key="item.TeacherID"
        :label="`${item.TeacherName} ${(ShowTeacherNo ? ('(' + item.TeacherNo +'})') : '')}`"
        :value="item.TeacherID"
        :disabled="item.disabled"
      >
        <span>{{ item.TeacherName }}</span>
        <span>{{ item.TeacherNo }}</span>
        <span v-if="item.CSID !=='' && item.CSID !== ExCSID">已排课</span>
      </el-option>
    </el-select>
  </el-tooltip>
</template>

<script>
import api from "@/scripts/api";
import moment from "moment";

export default {
  props: {
    size: {
      type: String,
      default: "mini"
    },
    /**
     * 允许创建
     */
    ArrowCreate: {
      type: Boolean,
      default: false
    },
    /**
     * 多选
     */
    multiple: {
      type: Boolean,
      default: false
    },
    /**
     * 排课日期
     */
    ScheduleDate: {},
    TooltipPlacement: {
      type: String,
      default: "right"
    },
    /**
     * 排除课表
     */
    ExCSID: {
      type: String,
      default: ""
    },
    // 显示老师工号
    ShowTeacherNo: {
      type: Boolean,
      default: true
    },
    /**
     * 排课节次
     */
    ScheduleLesson: {},
    value: {},
    Placeholder: {
      type: String,
      default: "请输入关键词"
    }
  },
  watch: {
    value(newVal, oldVal) {
      this.Value = newVal;
      if (newVal !== oldVal) {
        if (this.multiple) {
          if (JSON.stringify(newVal) !== JSON.stringify(oldVal)) {
            this.ReVerify();
          }
        } else {
          this.LoadData();
        }
      }
    },
    ScheduleDate(newVal, oldVal) {
      if (newVal !== oldVal) this.ReVerify();
    },
    ScheduleLesson(newVal, oldVal) {
      if (newVal !== oldVal) this.ReVerify();
    }
  },
  data() {
    return { loading: false, Value: this.value, Datas: [] };
  },
  mounted() {
    this.LoadData();
  },
  methods: {
    ChangeHandle(val) {
      this.$emit("update:value", this.Value);
      this.$emit("Filter", this.Value);

      let text = "";

      if (this.multiple) {
        const texts = [];

        this.Value.forEach(element => {
          const findC = this.Datas.find(c => c.TeacherID === element);
          if (findC) {
            texts.push(
              `${findC.TeacherName}${
                this.ShowTeacherNo ? " (" + findC.TeacherNo + "})" : ""
              }`
            );
          } else {
            texts.push(element);
          }
        });

        text = JSON.stringify(texts);
        const reg = new RegExp('"', "g"); // g代表全部
        text = text.slice(1, text.length - 1).replace(reg, "");
      } else {
        const findC = this.Datas.find(c => c.TeacherID === val);
        if (findC) {
          text = `${findC.TeacherName}${
            this.ShowTeacherNo ? " (" + findC.TeacherNo + "})" : ""
          }`;
        } else {
          text = this.Value;
        }
      }

      this.$emit("update:text", text);
    },
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        this.LoadData(query);
      } else {
        this.Datas = [];
      }
    },
    /**
     * 重新验证已选中的值
     */
    async ReVerify() {
      this.loading = true;
      if (this.Value && this.Value.length > 0) {
        this.Value = [];
        const res = await api.Teacher.GetDataByName(
          "",
          moment(this.ScheduleDate).format("YYYY-MM-DD"),
          this.ScheduleLesson,
          this.multiple
            ? JSON.stringify(this.value)
            : `[${JSON.stringify(this.value)}]`
        );
        if (res) {
          if (res.Datas) {
            res.Datas.forEach(element => {
              this.$set(
                element,
                "disabled",
                !(element.CSID === "" || element.CSID === this.ExCSID)
              );
              if (element.CSID === "" || element.CSID === this.ExCSID) {
                if (this.value.indexOf(element.TeacherID) >= 0) {
                  this.Value.push(element.TeacherID);
                }
              }
            });
          }

          this.Datas = res.Datas;

          if (this.ArrowCreate) {
            this.value.forEach(val => {
              if (this.Value.indexOf(val) < 0) {
                this.Value.push(val);
              }
            });
          }

          this.$emit("update:value", this.Value);
        }
        this.loading = false;
      }
    },
    async LoadData(name) {
      const res = await api.Teacher.GetDataByName(
        name,
        moment(this.ScheduleDate).format("YYYY-MM-DD"),
        this.ScheduleLesson,
        this.multiple
          ? JSON.stringify(this.value)
          : `[${JSON.stringify(this.value)}]`
      );
      if (res) {
        res.Datas.forEach(element => {
          this.$set(
            element,
            "disabled",
            !(element.CSID === "" || element.CSID === this.ExCSID)
          );
        });
        this.Datas = res.Datas;
      }
      this.loading = false;
    }
  }
};
</script>

